<template>
	<div class="large-deposit-transfer-out-transfer">
		<mt-cell title="大额存单账户：" :value="account1"></mt-cell>
		<mt-cell title="存单本金：" :value="account1"></mt-cell>
		<mt-cell title="提取支取本息合计：" :value="account1"></mt-cell>
		<mt-cell title="到期本息合计：" :value="account1"></mt-cell>
		<mt-cell title="出售金额：" :value="account1"></mt-cell>
		<cell-password :options="cellPassword" ></cell-password>
		<div class="page-part">
      		<mt-field class="code" label="验证码：" placeholder="输入验证码" :attr="{ maxlength: 6 }">
        		<mt-button type="primary">获取验证码</mt-button>
      		</mt-field>
    	</div>
    	<div class="field-warn">
    		提示：验证码为6位数字
    	</div>
		<div class="field-action">
            <submit-button @submit="submit" :submitStatus="submitStatus" value="获取转让码（有效期为当天）"></submit-button>
		</div>
	</div>
</template>

<script>

export default{
	data() {
		return {
			account1: '',
			account2: '',
			code: '',
            submitStatus: false,
            cellPassword: {
                title: '交易密码：',
                rule: 'number'
            }
		};
	},
    watch: {
        account1: function(val) {
            // if(val){
                this.submitStatus = true;
            // }else{
            //     this.submitStatus = false;
            // }
        }
    },
	methods: {
		showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(data => {
                this.selectorPaymentAccount.checked = data.item;
                this.account2 = data.item.account;
            }, data => {
            });
        },
        submit() {
        	this.$router.push(`/large-deposit/transfer/out-success`);
        },
        back() {
        	history.back();
        }
	},
	created() {
		this.account1 = this.$route.params.account;
	}
};

</script>

<style lang="scss">
.large-deposit-transfer-out-transfer{
	.code{
		.mint-field-other{
			button{
				height: 70%;
				margin-top: 6%;
				margin-right: 16px;
			}
		}
	}
}
</style>


