<template>
	<div class="large-deposit-buy-success">
		<mt-cell title="提示信息"></mt-cell>
		<div class="success-contain">
			<mt-cell title="交易成功！"></mt-cell>
			<mt-cell title="大额存单账户：" :value="detail.name"></mt-cell>
			<mt-cell title="存单本金：" :value="detail.liveAccount"></mt-cell>
		</div>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/live-keep';

export default {
	data() {
		return {
			detail: {
				name: '',
				liveAccount: '',
				money1: '',
				money2: '',
				type: '',
				date: '',
				date1: '',
				date2: '',
				rate: '',
				interest1: '',
				interest2: '',
				giveType: ''
			}
		};
	},
	methods: {
		back() {
			this.$router.push('/large-deposit/buy');
		},
		successKeep2live(account) {
			service.successKeep2live({
				account: account
			}).then(data => {
				this.detail = data.data.data;
			});
		}
	},
	created() {
		this.successKeep2live(this.$route.params.account);
	}
};

</script>

<style lang="scss" scoped>

.large-deposit-buy-success{
	.mint-cell:after{
		height: 0;
	}
	.success-contain{
		border-top: 1px solid #d9d9d9;
		border-bottom: 1px solid #d9d9d9;
	}
}

</style>


