<template>
	<div class="large-deposit-transfer-in-index">
		<mt-cell title="卡号：" :value="detail.number"></mt-cell>
		<mt-field label="转让码：" placeholder="请输入转让码"></mt-field>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
			<mt-button @click.native="submit" type="primary">确定</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/live-keep';

export default {
	data() {
		return {
			detail: {
				liveAccount: '',
				keepAccount: '',
				money: '',
				type: '',
				status: '',
				date: '',
				date1: '',
				date2: '',
				rate: ''
			},
			inputs: {
				giveType: '0',
				giveMoney: '',
				idCard: ''
			}
		};
	},
	methods: {
		submit() {
			this.submitKeep2live();
		},
		submitKeep2live() {
			service.submitKeep2live()
			.then(data => {
				this.$router.push(`/large-deposit/transfer/in-detail/${this.account}`);
			});
		},
		back() {
			history.back();
		},
		getKeepDetail(account) {
			service.getKeepDetail({
				account: account
			}).then(data => {
				this.detail = data.data.data;
				if(this.detail.keepType != 2){
					this.inputs.giveType = '1';
				}
			});
		}
	},
	created() {
		this.getKeepDetail(this.$route.params.account);
	}
};

</script>


